<template>
  <transition name="slide">
    <div class="face-recognition-main">
      <c-nav-bar class="address-nav-bar">
        <i slot="left" class="nav-bar-left iconfont icon-fanhui1"></i>
        <span slot="center" class="nav-bar-center">
          人脸识别
        </span>
      </c-nav-bar>
      <div class="face-recogition-content">
        <video id="video" width="320" height="320" webkit-playsinline="true" playsinline="true"></video>
        <canvas id="canvas" width="375" height="320"></canvas>
        <div class="hole"></div>
        <button class="btnPicture" @click="picture">
          获取图片
        </button>
        <div class="submit-btn">
          <van-button v-show="!isBegin" size="large" @click="play" class="btn" type="default">
            开始
          </van-button>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
export default {
  data() {
    return {
      isBegin: false
    }
  },
  mounted() {
    setTimeout(() => {
      this.init()
    },20)
  },
  methods: {
    init() {
      this.video = document.getElementById('video')
      this.context = document.getElementById('canvas').getContext('2d')
      navigator.mediaDevices.getUserMedia({
        'audio': false,
        'video': { 'facingMode': 'user' } //调用前置摄像头，后置摄像头使用video: { facingMode: { exact: "environment" } }
      }).then((mediaStream)=>{
        this.getVideoStream(mediaStream)
      }).catch((err) => {
        alert(err)
      })
    },
    getVideoStream(mediaStream) {
      this.video.srcObject = mediaStream
      // this.video.onloadedmetadata = () => {
      //   // video.controls = "controls";
      //   // this.video.play();
      // }
    },
    play() {
      this.video.play();
    },
    picture(){
      this.context.drawImage(this.video, 0, 0, 375, 320)
    }
  }
}
</script>
<style lang="scss" scoped>
.face-recognition-main {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index:1001;
  background: $background-color;
  .face-recogition-content {
    position: absolute;
    top: 44px;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
    #video {
      margin: 0 auto;
    }
    #canvas {
      display: none;
    }
    .hole {
      width: 100vw;
      height: 320px;
      position: absolute;
      top: 0;
      left: 0;
      &:after {
        position: absolute;
        height: 200px;
        width: 200px;
        left: 50%;
        top: 50%;
        content: '';
        transform: translate(-50%,-50%);
        border-radius: 50%;
        border: 60px solid #f5f5f5;
      }
    }
    .submit-btn {
      position: absolute;
      bottom: 20px;
      left: 0;
      right: 0
    }
    .btnPicture {
      position: absolute;
      bottom: 100px;
      left: 0;
      right: 0
    }
  }
}
</style>
